import '../Style/Header.css'
import logo from '../Img/logo.png'
import { useState } from 'react';
import { useEffect } from 'react';
import {
  Link
} from "react-router-dom";

const Header = () =>{
  const [styleBlack, setStyleBlack] = useState('transparent')
  useEffect(() => {
    const scrollListener = ()=>{
      if(window.scrollY>10){
        setStyleBlack('#141414')
      }
      else{setStyleBlack('transparent')}
    }
    window.addEventListener('scroll',scrollListener);
    return () => {
      window.removeEventListener('scroll',scrollListener)
    }
  }, []);   
  return(
    <header style={{width: "100%" ,backgroundColor: styleBlack,transition: ".35s"}}>
        <Link to='/Mflix'>
        <img src={logo} alt={'Mflix'}/>
        </Link>   
        <div className='escolher'>
          <Link to='/filmes' exact className='button'>Filmes</Link>
          <Link to='/series' exact className='button'>Series</Link>
        </div>
    </header>
      )
    }
    export default Header