import './Style/App.css';
import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import Series from './Components/Series';
import Filmes from './Components/Filmes';
import Header from './Components/Header';
import Tmdb from './Components/Tmdb';
import {GenresSeries} from './Dados/GenresSeries';
import {GenresFilmes} from './Dados/GenresFilmes';
import {BibliotecaSerie} from './Dados/BibliotecaSerie';
import {BibliotecaFilmes} from './Dados/BibliotecaFilmes';
import {
  BrowserRouter,
  Switch,
  Route,
} from "react-router-dom";


const App = () => {
  const[infosSeries,setnfosSeries] = useState(null)
  const[infosMovie,setnfosMovie] = useState(null)
  
  window.open = function (url, windowName, windowFeatures) {
    alert('not opening a window: '+windowName);
  } 
  
  const limparLocalStory = (props) =>{
    if(JSON.parse(localStorage.getItem('versao')) < props){localStorage.removeItem("Arry-movie");localStorage.removeItem("Arry-tv")}
    localStorage.setItem('versao', JSON.stringify(props))   
  }   
  const TmdbBusca = async (props)=>
  { 
    let Biblioteca = props[0]    
    let Genre = props[1]
    let tipo = props[2]
    //-----------    
    let buscarlocal = JSON.parse(localStorage.getItem("Arry-"+tipo))
    if(buscarlocal){
      if(tipo === 'movie')
      {setnfosMovie(buscarlocal);}
      else
      {setnfosSeries(buscarlocal)}   
    }
    else {
      let busca = await Tmdb({Biblioteca, Genre,tipo})
      if(tipo === 'movie')
      {setnfosMovie(busca)}
      else
      {setnfosSeries(busca)}  
    }
  }
  useEffect(() => {
    
    const intervalo = (props)=>{ 
      setTimeout(() => { 
      TmdbBusca([
        BibliotecaFilmes.reverse().slice(0, props),
        GenresFilmes,
        'movie'
      ])
      
       
        TmdbBusca([
          BibliotecaSerie.slice(0, props),
          GenresSeries,
          'tv'
        ])
      },300)
    }
    //---start
    intervalo(500)
    
    var data = new Date();
    var mes = data.getMonth() + 1
    var ano = data.getFullYear();
    let dataAtual = mes + ano;
    console.log({dataAtual})
    let versao = dataAtual
    limparLocalStory(versao)
  },[]);
  
  return (
    <BrowserRouter>
    <div className="App">
    <Header/>
    <Switch>          
    <Route path="/series">
    <Series infos={infosSeries} />
    {!infosSeries &&
      <img src='https://minuta.gestaourbana.prefeitura.sp.gov.br/piu-setor-central/static/galeria/carregando.gif' alt={'carregando'} 
      style={{
        filter: 'invert(1)',
        objectFit: 'cover',
        width:'100%',
        height:'99.49vh',
        display: 'absolute',
        top:'0px',
        left:'0px'
      }} />
    }
    </Route>
    <Route path="/">
    <Filmes infos={infosMovie} />
    {!infosMovie &&
      <img src='https://minuta.gestaourbana.prefeitura.sp.gov.br/piu-setor-central/static/galeria/carregando.gif' alt={'carregando'} 
      style={{
        filter: 'invert(1)',
        objectFit: 'cover',
        width:'100%',
        height:'100vh',
        display: 'absolute',
        top:'0px',
        left:'0px',
        zIndex:'9999'
      }} />
    }
    </Route>
    </Switch>        
    </div>
    </BrowserRouter>
    )
  }
  export default App;